import Vex from 'vexflow';
const {StaveNote} = Vex.Flow;

export const vexify = input => {
  if(Array.isArray(input)) {
    let returnArray = [];
    input.forEach(note => returnArray.push(vexify(note)));
    return returnArray;
  }

  return new StaveNote({
    clef: "treble",
    keys: ["a/4"],
    duration: '8' + (!input ? 'r' : '')
  });
};



